.c-flag {
  border: 1px solid lightgrey;
  display: inline-block;
}
.c-flag.type-circle {
  border-radius: 50%;
  overflow: hidden;
}
.c-flag.type-circle.size-M {
  width: 25px;
  height: 25px;
  min-width: 25px;
}
.c-flag.type-circle.size-M img {
  height: 25px;
  margin-left: -6px;
}
.c-flag.type-circle.size-L {
  width: 40px;
  height: 40px;
  min-width: 40px;
}
.c-flag.type-circle.size-L img {
  height: 40px;
  margin-left: -8px;
}
.c-flag.type-original.size-M img {
  height: 25px;
  min-height: 25px;
  width: auto;
}
.c-flag.type-original.size-L img {
  height: 40px;
  min-height: 40px;
  width: auto;
}
.c-flaglist {
  display: flex;
  flex-wrap: nowrap;
}
